
import { onMounted, ref } from "vue";
import { evaluateValue } from "./validation";
import { delay } from "@/helpers/HelperFunctions.ts";

export default {
  name: "RangeFormField",
  props: {
    element: Object,
    elementId: String,
  },
  setup(props, { emit }) {
    const emitEvent = (event) => {
      delay(
        "formFill",
        () => {
          evaluateValue(props);
          emit("updateElement", event);
        },
        500
      );
    };
    onMounted(() => {
      evaluateValue(props);
    });

    return { emitEvent };
  },
};
