
export default {
  name: "Button",
  props: {
    label: String,
    callback: Function,
    data: Object,
    type: String,
  },
  setup(props) {
    let buttonType = props.type;
    if (!buttonType) buttonType = "default";

    let dataAttribute = {};
    if (props.data) {
      dataAttribute = {
        [`data-${props.data.name}`]: props.data.value,
      };
    }

    return { buttonType, dataAttribute };
  },
};
