
import { onMounted, ref } from "vue";

export default {
  name: "RangeFormField",
  props: {
    element: Object,
    elementId: String,
  },
  setup(props, { emit }) {
    // console.error(props);
    const emitEvent = (event) => {
      emit("updateElement", event);
    };

    return { emitEvent };
  },
};
